
import { Vue, Component } from 'vue-property-decorator';
import { tplConfigList } from '@/mock/data'
import FolderItem from '@/components/FolderItem/index.vue'
import FileList from '@/components/FileList/index.vue'
@Component({
  components: {
    FolderItem,
    FileList
  }
})
export default class Container extends Vue {
  get selectFloder () {
    return this.$store.state.selectFloder
  }
  get tplConfigList () {
    return this.$store.state.tplConfigList
  }

  exit () {
    this.$store.commit('resetState')
  }

  created () {
    if (process.env.NODE_ENV === 'development') {
      this.$store.commit('setTplConfigList', tplConfigList)
    }
    // 监听vscode传入的值
    window.addEventListener('message', event => {
      const message = event.data

      switch (message.command) {
        case 'initData':
          this.$store.commit('setTplConfigList', message.data)
      }
    })
  }
}
