import { State, getDefaultState } from './state';
import { MutationTree } from 'vuex';

const mutations: MutationTree<State> = {
  setFolderData (state, payload) {
    state.folderData = payload.item
    state.selectFloder.push({
      name: payload.key,
      children: payload.item
    })
  },
  setTplConfigList (state, payload) {
    state.tplConfigList = payload
  },
  resetState (state) {
    Object.assign(state, getDefaultState(), {
      tplConfigList: state.tplConfigList
    })
  }
};

export default mutations;
