import { ConfigItem, TplConfigList, SelectFloder } from '../pages/Container/interface'
export interface State {
  folderData: ConfigItem[]
  tplConfigList: TplConfigList
  selectFloder: SelectFloder[]
}

export const getDefaultState = () => {
  return {
    folderData: [] as ConfigItem[],
    selectFloder: [] as SelectFloder[],
    tplConfigList: {} as TplConfigList
  }
}

const state: State = getDefaultState();

export default state;
