
import { Vue, Component, Prop } from 'vue-property-decorator'
import { ConfigItem } from '@/pages/Container/interface'
import folderPng from '@/assets/image/floder.png'
@Component
export default class FolderItem extends Vue {
  @Prop() name!: string
  @Prop() item!: ConfigItem
  folderPng = folderPng
  save () {
    // 保存当前选择的文件夹内容
    this.$store.commit('setFolderData', {
      item: this.item,
      key: this.name
    })
  }
}
